.App {
    margin: 2rem 10rem;
}

table {
    border-spacing: 0;
    border: 1px solid black;
}

tr:last-child td {
    border-bottom: 0;
}

th,
td {
    margin: 0;
    padding: 0.5rem;
    border-bottom: 1px solid black;
    border-right: 1px solid black;
}

th:last-child,
td:last-child {
    border-right: 0;
}
